.App {
    font-family: 'DM Sans', sans-serif;
    background-color: #f8f8f8;
    min-height: 100vh;
}

.Main {

    padding-top: 100px;
}

.Content {
    background-color: #ffffff;
    padding: 10px;
    border-radius: 15px;
    border: 1px solid #000000;
}

.FillerImage {
    object-fit: cover;
    height: 100%;
    width: 100%;
    border: 2px solid #fa9455;
    border-radius: 15px;
}

.Image {
    max-height: 40vh;
    max-width: 100%;
    object-fit: contain;
}

* {
    box-sizing: border-box;
}

a {
    color: #000000;
    transition: color ease-in-out 0.3s;
}

a:hover {
    text-decoration: none;
}

a:hover i {
    cursor: pointer;
    color: #219EBC;
    transition: color ease-in-out 0.3s;
}

input[type="file"] {
    display: none;
}

i {
    color: #000000;
    font-size: 1.6rem;
    padding: 7px;
    vertical-align: middle;
}

i:hover {
    cursor: pointer;
    transition: color ease-in-out 0.3s;
    color: #219EBC;
}

figure {
    text-align: center;
    display: inline-block;
}

/* scrollbar */
*::-webkit-scrollbar {
    width: 5px;
}

*::-webkit-scrollbar-track {
    background: #f1f1f1;
}

*::-webkit-scrollbar-thumb {
    background: #888;
}

*::-webkit-scrollbar-thumb:hover {
    background: #555;
}